'use client';

import { AlertTriangle, CalendarClock, Activity } from 'lucide-react';
import clsx from 'clsx';

const formatDate = (value) => {
  if (!value) return 'No due date';
  try {
    return new Intl.DateTimeFormat('en', { month: 'short', day: 'numeric' }).format(new Date(value));
  } catch (error) {
    return 'No due date';
  }
};

const getWorkload = (member) => {
  if (typeof member?.workload === 'number') {
    return Math.round(member.workload);
  }
  const capacity = member?.capacity || 5;
  const assigned =
    (Array.isArray(member?.tasks) ? member.tasks.length : member?.taskCount || 0) || 0;
  if (!capacity) {
    return 0;
  }
  return Math.min(100, Math.round((assigned / capacity) * 100));
};

export default function StudioInsights({ projects = [], teamMembers = [] }) {
  const now = Date.now();
  const upcomingWindow = now + 1000 * 60 * 60 * 24 * 7;

  const overdue = projects
    .filter(
      (project) =>
        project?.dueDate &&
        new Date(project.dueDate).getTime() < now &&
        project.status !== 'Completed',
    )
    .sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate));

  const dueSoon = projects
    .filter((project) => {
      if (!project?.dueDate || project.status === 'Completed') return false;
      const time = new Date(project.dueDate).getTime();
      return time >= now && time <= upcomingWindow;
    })
    .sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate));

  const capacityAlerts = teamMembers
    .map((member) => ({ ...member, computedWorkload: getWorkload(member) }))
    .filter((member) => member.computedWorkload >= 90)
    .sort((a, b) => b.computedWorkload - a.computedWorkload);

  const deliveryTimeline = [...overdue, ...dueSoon].slice(0, 4);
  const watchlist = capacityAlerts.slice(0, 3);

  const statCards = [
    {
      label: 'Overdue briefs',
      value: overdue.length,
      icon: AlertTriangle,
      tone: 'text-rose-200',
      badge: 'bg-rose-500/15 border border-rose-400/40',
    },
    {
      label: 'Due this week',
      value: dueSoon.length,
      icon: CalendarClock,
      tone: 'text-amber-200',
      badge: 'bg-amber-500/15 border border-amber-400/40',
    },
    {
      label: 'Capacity alerts',
      value: capacityAlerts.length,
      icon: Activity,
      tone: 'text-sky-200',
      badge: 'bg-sky-500/15 border border-sky-400/40',
    },
  ];

  return (
    <section className="glass-panel h-full rounded-3xl p-6 shadow-glass">
      <header className="flex items-start justify-between gap-3">
        <div>
          <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Insights</p>
          <h2 className="text-2xl font-semibold text-white">Studio pulse</h2>
          <p className="mt-1 text-sm text-slate-400">Auto-generated alerts from live project data.</p>
        </div>
      </header>

      <div className="mt-5 grid gap-3 sm:grid-cols-3">
        {statCards.map((card) => (
          <article
            key={card.label}
            className="rounded-2xl border border-white/10 bg-white/5 p-4 backdrop-blur"
          >
            <div className="flex items-center gap-2 text-slate-300">
              <card.icon size={16} className={card.tone} />
              <span className="text-xs uppercase tracking-[0.3em]">{card.label}</span>
            </div>
            <p className={clsx('mt-3 text-3xl font-semibold', card.tone)}>{card.value}</p>
          </article>
        ))}
      </div>

      <div className="mt-6 space-y-4">
        <section className="rounded-2xl border border-white/10 bg-white/5 p-4">
          <div className="flex items-center justify-between">
            <p className="text-sm font-semibold text-white">Delivery timeline</p>
            <span className="text-xs text-slate-400">Next 7 days</span>
          </div>
          <div className="mt-3 space-y-3">
            {deliveryTimeline.length ? (
              deliveryTimeline.map((project) => (
                <div
                  key={project._id}
                  className="flex items-center justify-between gap-3 rounded-2xl border border-white/10 bg-studio-ink/40 px-3 py-2"
                >
                  <div>
                    <p className="text-sm font-semibold text-white">{project.name}</p>
                    <p className="text-xs text-slate-400">
                      Due {formatDate(project.dueDate)} • {project.status || 'Planned'}
                    </p>
                  </div>
                  <span className="rounded-full bg-white/10 px-3 py-1 text-xs text-slate-100">
                    {project.taskSummary?.completed || 0}/{project.taskSummary?.total || 0} tasks
                  </span>
                </div>
              ))
            ) : (
              <p className="rounded-2xl border border-dashed border-white/15 p-4 text-center text-sm text-slate-400">
                All upcoming deadlines are on track.
              </p>
            )}
          </div>
        </section>

        <section className="rounded-2xl border border-white/10 bg-white/5 p-4">
          <div className="flex items-center justify-between">
            <p className="text-sm font-semibold text-white">Capacity watchlist</p>
            <span className="text-xs text-slate-400">Target &lt; 90%</span>
          </div>
          <div className="mt-3 space-y-3">
            {watchlist.length ? (
              watchlist.map((member) => (
                <div
                  key={member._id}
                  className="flex items-center justify-between gap-3 rounded-2xl border border-white/10 bg-studio-ink/40 px-3 py-2"
                >
                  <div>
                    <p className="text-sm font-semibold text-white">{member.name}</p>
                    <p className="text-xs text-slate-400">{member.role}</p>
                  </div>
                  <span className="rounded-full bg-rose-500/10 px-3 py-1 text-xs font-semibold text-rose-100">
                    {member.computedWorkload}% load
                  </span>
                </div>
              ))
            ) : (
              <p className="rounded-2xl border border-dashed border-white/15 p-4 text-center text-sm text-slate-400">
                No team members are over capacity.
              </p>
            )}
          </div>
        </section>
      </div>
    </section>
  );
}
