'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';
import { Users, UserPlus, Loader2 } from 'lucide-react';
import clsx from 'clsx';

const workloadToColor = (percentage) => {
  if (percentage < 60) return 'bg-emerald-500/30';
  if (percentage < 90) return 'bg-amber-500/30';
  return 'bg-rose-500/40';
};

export default function TeamOverview({ teamMembers = [], onCreate = async () => {} }) {
  const [form, setForm] = useState({ name: '', role: 'Architect', capacity: 5 });
  const [submitting, setSubmitting] = useState(false);

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!form.name.trim()) return;
    try {
      setSubmitting(true);
      await onCreate(form);
      setForm({ name: '', role: 'Architect', capacity: 5 });
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <section className="glass-panel rounded-3xl p-6 shadow-glass">
      <div className="flex items-center gap-3">
        <div className="rounded-2xl bg-emerald-500/20 p-3 text-emerald-300">
          <Users size={24} />
        </div>
        <div>
          <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Team</p>
          <h2 className="text-2xl font-semibold text-white">Studio Capacity</h2>
        </div>
      </div>

      <div className="mt-4 space-y-4">
        {teamMembers.length ? (
          teamMembers.map((member) => {
            const tasks = Array.isArray(member.tasks) ? member.tasks : [];
            const capacity = member.capacity || 5;
            const derivedCount = tasks.length || member.taskCount || 0;
            const computedWorkload = capacity ? Math.round((derivedCount / capacity) * 100) : 0;
            const workload = Math.min(100, member.workload ?? computedWorkload);
            return (
              <motion.article
                key={member._id}
                initial={{ opacity: 0, x: -12 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.2 }}
                className="rounded-2xl border border-white/10 bg-white/5 p-4"
              >
                <div className="flex items-center justify-between gap-2">
                  <div>
                    <h3 className="text-lg font-semibold text-white">{member.name}</h3>
                    <p className="text-sm text-slate-400">{member.role}</p>
                  </div>
                  <span
                    className={clsx(
                      'rounded-full px-3 py-1 text-xs font-semibold uppercase tracking-wide',
                      workload < 60
                        ? 'bg-emerald-500/20 text-emerald-100'
                        : workload < 90
                        ? 'bg-amber-500/20 text-amber-100'
                        : 'bg-rose-500/20 text-rose-100',
                    )}
                  >
                    {workload}% load
                  </span>
                </div>
                <div className="mt-3 h-2.5 rounded-full bg-white/5">
                  <div
                    className={clsx('h-full rounded-full transition-all', workloadToColor(workload))}
                    style={{ width: `${Math.min(100, workload)}%` }}
                  />
                </div>
                <p className="mt-2 text-xs text-slate-400">
                  {derivedCount} / {capacity} tasks
                </p>
                {tasks.length > 0 && (
                  <div className="mt-3 flex flex-wrap gap-2">
                    {tasks.slice(0, 3).map((task) => (
                      <span key={task._id} className="rounded-full bg-white/10 px-3 py-1 text-xs text-slate-200">
                        {task.name}
                      </span>
                    ))}
                    {tasks.length > 3 && (
                      <span className="text-xs text-slate-400">+{tasks.length - 3} more</span>
                    )}
                  </div>
                )}
              </motion.article>
            );
          })
        ) : (
          <p className="rounded-2xl border border-dashed border-white/20 p-6 text-center text-slate-400">
            Add your core team to start tracking workload.
          </p>
        )}
      </div>

      <form onSubmit={handleSubmit} className="mt-6 space-y-3 rounded-2xl border border-white/10 bg-white/5 p-4">
        <div className="flex items-center gap-2 text-sm text-slate-300">
          <UserPlus size={16} /> <span>New teammate</span>
        </div>
        <input
          className="w-full rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-emerald-400/60 focus:outline-none"
          placeholder="Full name"
          value={form.name}
          onChange={(event) => setForm((prev) => ({ ...prev, name: event.target.value }))}
        />
        <input
          className="w-full rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-emerald-400/60 focus:outline-none"
          placeholder="Role (e.g. Project Architect)"
          value={form.role}
          onChange={(event) => setForm((prev) => ({ ...prev, role: event.target.value }))}
        />
        <label className="block text-sm text-slate-300">
          Weekly capacity
          <input
            type="number"
            min={1}
            max={10}
            className="mt-1 w-full rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-emerald-400/60 focus:outline-none"
            value={String(form.capacity)}
            onChange={(event) =>
              setForm((prev) => ({
                ...prev,
                capacity: Math.max(1, Number(event.target.value) || 1),
              }))
            }
          />
        </label>
        <button
          type="submit"
          disabled={submitting}
          className="inline-flex w-full items-center justify-center gap-2 rounded-xl bg-gradient-to-r from-emerald-400 to-sky-400 px-4 py-2 font-semibold text-studio-ink disabled:cursor-not-allowed disabled:opacity-60"
        >
          {submitting ? <Loader2 size={16} className="animate-spin" /> : <UserPlus size={16} />}
          {submitting ? 'Adding' : 'Add team member'}
        </button>
      </form>
    </section>
  );
}
