'use client';

import { useEffect, useMemo, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { BadgeCheck, Loader2, Plus, UserRound, Trash2 } from 'lucide-react';
import clsx from 'clsx';
import { api } from '../lib/api';

const priorityTone = {
  High: 'bg-rose-500/10 border border-rose-400/40 text-rose-100',
  Medium: 'bg-amber-500/10 border border-amber-400/40 text-amber-100',
  Low: 'bg-emerald-500/10 border border-emerald-400/40 text-emerald-100',
};

export default function TaskPanel({
  project,
  teamMembers = [],
  onProjectUpdate,
  onTeamRefresh = () => {},
}) {
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({ name: '', assigneeId: '', priority: 'Medium' });
  const [submitting, setSubmitting] = useState(false);
  const [busyTaskId, setBusyTaskId] = useState(null);

  useEffect(() => {
    let ignore = false;
    const load = async () => {
      if (!project?._id) {
        setTasks([]);
        return;
      }
      setLoading(true);
      try {
        const data = await api.getTasks(project._id);
        if (!ignore) {
          setTasks(data);
        }
      } catch (error) {
        console.error('Failed to load tasks', error);
      } finally {
        if (!ignore) {
          setLoading(false);
        }
      }
    };

    load();
    return () => {
      ignore = true;
    };
  }, [project?._id]);

  const summary = useMemo(() => {
    const completed = tasks.filter((task) => task.status === 'Complete').length;
    return {
      completed,
      total: tasks.length,
    };
  }, [tasks]);

  const pushProjectUpdate = (updatedProject, nextTasks) => {
    if (!updatedProject || !onProjectUpdate) return;
    const completed = nextTasks.filter((task) => task.status === 'Complete').length;
    onProjectUpdate({
      ...updatedProject,
      taskSummary: {
        total: nextTasks.length,
        completed,
      },
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!project?._id || !form.name.trim()) return;
    try {
      setSubmitting(true);
      const payload = {
        name: form.name,
        projectId: project._id,
        assigneeId: form.assigneeId || undefined,
        priority: form.priority,
      };
      const { task, project: updatedProject } = await api.createTask(payload);
      setTasks((prev) => {
        const next = [task, ...prev];
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
      setForm({ name: '', assigneeId: '', priority: 'Medium' });
    } catch (error) {
      console.error('Failed to create task', error);
    } finally {
      setSubmitting(false);
    }
  };

  const toggleStatus = async (task) => {
    try {
      setBusyTaskId(task._id);
      const nextStatus = task.status === 'Complete' ? 'Incomplete' : 'Complete';
      const { task: updatedTask, project: updatedProject } = await api.toggleTaskStatus(
        task._id,
        nextStatus,
      );
      setTasks((prev) => {
        const next = prev.map((item) => (item._id === task._id ? updatedTask : item));
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
    } catch (error) {
      console.error('Failed to toggle task', error);
    } finally {
      setBusyTaskId(null);
    }
  };

  const removeTask = async (task) => {
    try {
      setBusyTaskId(task._id);
      const { project: updatedProject } = await api.deleteTask(task._id);
      setTasks((prev) => {
        const next = prev.filter((item) => item._id !== task._id);
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
    } catch (error) {
      console.error('Failed to remove task', error);
    } finally {
      setBusyTaskId(null);
    }
  };

  if (!project) {
    return (
      <section className="glass-panel rounded-3xl p-6 text-center text-slate-400">
        <p>Select a project to see its task board.</p>
      </section>
    );
  }

  return (
    <section className="glass-panel rounded-3xl p-6 shadow-glass">
      <div className="flex items-center justify-between gap-3">
        <div>
          <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Tasks</p>
          <h2 className="text-2xl font-semibold text-white">{project.name}</h2>
        </div>
        <div className="rounded-2xl border border-white/10 bg-white/5 px-4 py-2 text-sm text-slate-300">
          Auto progress&nbsp;
          <span className="font-semibold text-sky-200">{project.progress}%</span>
        </div>
      </div>

      <div className="mt-4 grid gap-3 sm:grid-cols-3">
        <SummaryBadge label="Total" value={summary.total} tone="text-sky-200" />
        <SummaryBadge label="Complete" value={summary.completed} tone="text-emerald-200" />
        <SummaryBadge
          label="Pending"
          value={Math.max(0, summary.total - summary.completed)}
          tone="text-amber-200"
        />
      </div>

      <form onSubmit={handleSubmit} className="mt-6 grid gap-3 rounded-2xl border border-white/10 bg-white/5 p-4 sm:grid-cols-3">
        <input
          className="rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-sky-400/60 focus:outline-none sm:col-span-2"
          placeholder="New task (e.g. Façade study)"
          value={form.name}
          onChange={(event) => setForm((prev) => ({ ...prev, name: event.target.value }))}
        />
        <select
          className="rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-sky-400/60 focus:outline-none"
          value={form.priority}
          onChange={(event) => setForm((prev) => ({ ...prev, priority: event.target.value }))}
        >
          {['High', 'Medium', 'Low'].map((level) => (
            <option key={level} value={level}>
              {level} priority
            </option>
          ))}
        </select>
        <select
          className="rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-sky-400/60 focus:outline-none sm:col-span-2"
          value={form.assigneeId}
          onChange={(event) => setForm((prev) => ({ ...prev, assigneeId: event.target.value }))}
        >
          <option value="">Unassigned</option>
          {teamMembers.map((member) => (
            <option key={member._id} value={member._id}>
              {member.name}
            </option>
          ))}
        </select>
        <button
          type="submit"
          disabled={submitting}
          className="inline-flex items-center justify-center gap-2 rounded-xl bg-gradient-to-r from-emerald-500 to-sky-400 px-4 py-2 font-semibold text-studio-ink transition hover:opacity-95 disabled:cursor-not-allowed disabled:opacity-60"
        >
          {submitting ? <Loader2 size={16} className="animate-spin" /> : <Plus size={16} />}
          {submitting ? 'Adding' : 'Add task'}
        </button>
      </form>

      <div className="subtle-scrollbar mt-6 space-y-3 overflow-y-auto">
        {loading ? (
          <div className="flex items-center justify-center rounded-2xl border border-white/10 bg-white/5 py-12 text-slate-400">
            <Loader2 className="animate-spin" />
          </div>
        ) : tasks.length ? (
          <AnimatePresence>
            {tasks.map((task) => (
              <motion.article
                key={task._id}
                initial={{ opacity: 0, y: 12 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -12 }}
                transition={{ duration: 0.2 }}
                className="rounded-2xl border border-white/10 bg-gradient-to-r from-white/5 to-white/0 p-4"
              >
                <div className="flex flex-wrap items-center justify-between gap-3">
                  <div>
                    <p
                      className={clsx(
                        'text-lg font-semibold',
                        task.status === 'Complete' ? 'text-emerald-200 line-through' : 'text-white',
                      )}
                    >
                      {task.name}
                    </p>
                    <div className="mt-1 flex flex-wrap items-center gap-2 text-xs text-slate-400">
                      <span
                        className={clsx(
                          'rounded-full px-3 py-1 font-semibold',
                          priorityTone[task.priority] || priorityTone.Medium,
                        )}
                      >
                        {task.priority}
                      </span>
                      <span className="inline-flex items-center gap-1 rounded-full bg-white/5 px-2 py-1">
                        <UserRound size={14} />
                        {task.assignee?.name || 'Unassigned'}
                      </span>
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <button
                      type="button"
                      onClick={() => toggleStatus(task)}
                      disabled={busyTaskId === task._id}
                      className={clsx(
                        'inline-flex items-center gap-1 rounded-full px-3 py-1 text-sm',
                        task.status === 'Complete'
                          ? 'bg-emerald-500/20 text-emerald-100'
                          : 'bg-white/10 text-white',
                      )}
                    >
                      {busyTaskId === task._id ? (
                        <Loader2 size={14} className="animate-spin" />
                      ) : (
                        <BadgeCheck size={14} />
                      )}
                      {task.status === 'Complete' ? 'Done' : 'Mark done'}
                    </button>
                    <button
                      type="button"
                      onClick={() => removeTask(task)}
                      disabled={busyTaskId === task._id}
                      className="rounded-full border border-white/10 p-2 text-slate-400 hover:border-rose-300/40 hover:text-rose-200 disabled:opacity-50"
                    >
                      <Trash2 size={14} />
                    </button>
                  </div>
                </div>
              </motion.article>
            ))}
          </AnimatePresence>
        ) : (
          <p className="rounded-2xl border border-dashed border-white/20 p-6 text-center text-slate-400">
            No tasks yet – create the first assignment for this project.
          </p>
        )}
      </div>
    </section>
  );
}

function SummaryBadge({ label, value, tone }) {
  return (
    <div className="rounded-2xl border border-white/10 bg-white/5 p-4">
      <p className="text-xs uppercase tracking-[0.3em] text-slate-400">{label}</p>
      <p className={clsx('mt-2 text-2xl font-semibold', tone)}>{value}</p>
    </div>
  );
}
